/* global */
@font-face {
  font-family: "Montserrat";
  src: local("Montserrat Medium"), local("Montserrat-Medium"), url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../../fonts/Montserrat-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  src: local("Montserrat SemiBold"), local("Montserrat-SemiBold"), url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: local("Oswald Light"), local("Oswald-Light"), url("../fonts/Oswald-Light.woff2") format("woff2"), url("../fonts/Oswald-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: local("Oswald Regular"), local("Oswald-Regular"), url("../fonts/Oswald-Regular.woff2") format("woff2"), url("../fonts/Oswald-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: local("Oswald Medium"), local("Oswald-Medium"), url("../fonts/Oswald-Medium.woff2") format("woff2"), url("../fonts/Oswald-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
}
a {
  text-decoration: none;
}
img {
  height: auto;
}
ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.page {
  height: 100%;
  scroll-behavior: smooth;
}
.page__body {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  margin: 0;
  padding: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 20px;
  font-weight: 500;
  color: #444444;
}
@media (min-width: 768px) {
  .page__body {
    font-size: 16px;
    line-height: 24px;
  }
}
@media (min-width: 1200px) {
  .page__body {
    font-size: 18px;
    line-height: 28px;
  }
}
.container {
  margin: 0 6.25%;
}
@media (min-width: 768px) {
  .container {
    width: 668px;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .container {
    width: 1050px;
  }
}
/* colors */
/* viewports */
/* blocks */
.header {
  position: sticky;
  top: 0;
  z-index: 200;
  background-color: #ffffff;
}
@media (min-width: 1200px) {
  .header {
    border-bottom: 2px solid rgba(171, 171, 173, 0.43);
    box-shadow: 0 1px 4px rgba(171, 171, 173, 0.5);
  }
}
.promo {
  position: relative;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  min-height: 245px;
}
@media (min-width: 768px) {
  .promo {
    min-height: 380px;
  }
}
@media (min-width: 1200px) {
  .promo {
    min-height: 460px;
  }
}
.promo::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  min-height: 245px;
  width: 70vw;
  background-color: #2e5196;
  border-bottom-left-radius: 10px;
  box-shadow: 0 4px 5px rgba(0, 0, 0, 0.3);
  background-image: url("../img/promo-pattern.svg");
  z-index: 1;
}
@media (min-width: 768px) {
  .promo::before {
    min-height: 380px;
  }
}
@media (min-width: 1200px) {
  .promo::before {
    min-height: 460px;
  }
}
.promo::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  min-height: 224px;
  width: 70vw;
  background-color: #dce4f2;
  box-shadow: 0 4px 5px rgba(0, 0, 0, 0.2);
}
@media (min-width: 768px) {
  .promo::after {
    min-height: 350px;
  }
}
@media (min-width: 1200px) {
  .promo::after {
    min-height: 430px;
  }
}
.promo__container {
  min-height: 224px;
  max-width: 400px;
  margin: 0 6.25%;
  background-image: url("../img/promo-mobile.jpg"), linear-gradient(to right, #dce4f2 80%, rgba(220, 228, 242, 0) 80%);
  background-size: auto 224px;
  background-repeat: no-repeat;
  background-position: right center;
  justify-self: flex-start;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 30px 0;
  box-sizing: border-box;
  border-bottom-right-radius: 10px;
  z-index: 2;
  box-shadow: 0 4px 5px -5px rgba(0, 0, 0, 0.2);
}
@media (min-resolution: 2dppx) {
  .promo__container {
    background-image: url("../img/promo-mobile@2x.jpg"), linear-gradient(to right, #dce4f2 50%, rgba(220, 228, 242, 0) 50%);
    background-size: auto 224px;
  }
}
@media (min-width: 768px) {
  .promo__container {
    min-height: 350px;
    max-width: none;
    width: 668px;
    margin: 0 auto;
    background-image: url("../img/promo-tablet.jpg"), linear-gradient(to right, #dce4f2 50%, rgba(220, 228, 242, 0) 50%);
    background-size: auto 350px;
    padding: 50px 0;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .promo__container {
    background-image: url("../img/promo-tablet@2x.jpg"), linear-gradient(to right, #dce4f2 50%, rgba(220, 228, 242, 0) 50%);
    background-size: auto 350px;
  }
}
@media (min-width: 1200px) {
  .promo__container {
    min-height: 430px;
    background-image: url("../img/promo-desktop.jpg"), linear-gradient(to right, #dce4f2 50%, rgba(220, 228, 242, 0) 50%);
    background-size: auto 450px;
    padding: 60px 0;
    width: 1050px;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) and (min-resolution: 2dppx) {
  .promo__container {
    background-image: url("../img/promo-desktop@2x.jpg"), linear-gradient(to right, #dce4f2 50%, rgba(220, 228, 242, 0) 50%);
    background-size: auto 450px;
  }
}
.promo__text {
  font-size: 18px;
  line-height: 22px;
  font-weight: 600;
  color: #000000;
  margin-top: 0;
  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .promo__text {
    font-size: 32px;
    line-height: 36px;
    margin-bottom: 25px;
  }
}
@media (min-width: 1200px) {
  .promo__text {
    font-size: 38px;
    line-height: 44px;
    margin-bottom: 25px;
  }
}
.page-title {
  font-size: calc(1.1em + 1.6vw);
  line-height: calc(1em + 1.5vw);
  font-weight: 600;
  color: #000000;
  margin-top: 30px;
  margin-bottom: 20px;
}
@media (min-width: 768px) {
  .page-title {
    font-size: 28px;
    line-height: 36px;
    margin-top: 50px;
    margin-bottom: 30px;
  }
}
@media (min-width: 1200px) {
  .page-title {
    font-size: 32px;
    line-height: 45px;
    margin-top: 60px;
    margin-bottom: 40px;
  }
}
.page-title--inner {
  margin-left: 6.25%;
  margin-right: 6.25%;
}
@media (min-width: 768px) {
  .page-title--inner {
    width: 668px;
    margin-left: auto;
    margin-right: auto;
  }
}
@media (min-width: 1200px) {
  .page-title--inner {
    width: 1050px;
  }
}
@media (min-width: 768px) {
  .services {
    background-image: url("../img/back-pattern-tablet.svg");
    background-repeat: no-repeat;
    background-position: top right;
    background-size: auto 522px;
  }
}
@media (min-width: 1200px) {
  .services {
    background-image: url("../img/back-pattern-desktop.svg");
    background-size: auto 750px;
  }
}
.services__wrapper {
  padding-top: 1px;
  padding-bottom: 20px;
  border-bottom: 1px solid #e9f1ff;
}
@media (min-width: 768px) {
  .services__wrapper {
    padding-bottom: 0;
    border-bottom: none;
  }
}
@media (min-width: 768px) {
  .services__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
@media (min-width: 1200px) {
  .services__list {
    grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
    grid-auto-rows: auto;
    gap: 30px;
  }
}
.services__item {
  position: relative;
  display: flex;
  align-items: center;
  min-height: 93px;
  padding: 10px 55px 10px 20px;
  background-color: #e9f1ff;
  border-radius: 4px;
  box-sizing: border-box;
  margin-bottom: 20px;
  font-size: 13px;
}
.services__item:hover,
.services__item:focus {
  opacity: 0.8;
}
.services__item:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .services__item {
    width: 300px;
    min-height: 130px;
    margin-right: 68px;
    margin-bottom: 30px;
    align-items: flex-start;
    padding: 20px 60px 20px 25px;
    box-shadow: 3px 3px 4px #cdd6e7;
  }
}
@media (min-width: 1200px) {
  .services__item {
    width: 450px;
    min-height: 150px;
    padding: 40px 65px 40px 35px;
    box-shadow: 4px 4px 6px #cdd6e7;
    margin-right: 0;
    margin-bottom: 0;
  }
}
@media (min-width: 768px) {
  .services__item:nth-child(2n) {
    margin-right: 0;
  }
}
.services__link {
  color: #444444;
}
@media (min-width: 768px) {
  .services__link {
    font-size: 12px;
    line-height: 18px;
    font-weight: 600;
    text-transform: uppercase;
  }
}
@media (min-width: 1200px) {
  .services__link {
    font-size: 14px;
    line-height: 22px;
  }
}
.services__link::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.services__link::after {
  content: "";
  position: absolute;
  width: 15px;
  height: 35px;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
  background: url("../img/arrow-blue.svg") no-repeat;
  background-size: 15px 35px;
}
@media (min-width: 768px) {
  .services__link::after {
    top: auto;
    bottom: 0;
  }
}
@media (min-width: 1200px) {
  .services__link::after {
    width: 15px;
    height: 45px;
    background-size: 15px 45px;
  }
}
.footer {
  font-size: 10px;
  line-height: 14px;
  color: #ffffff;
  padding: 20px 0;
  background-color: #2a4883;
}
@media (min-width: 768px) {
  .footer {
    padding: 10px 0;
  }
}
@media (min-width: 1200px) {
  .footer {
    font-size: 12px;
    line-height: 16px;
    padding: 15px 0;
  }
}
.footer--inner {
  margin-top: 40px;
}
@media (min-width: 1200px) {
  .footer--inner {
    margin-top: 60px;
  }
}
.footer__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.footer__logo {
  display: none;
}
@media (min-width: 768px) {
  .footer__logo {
    display: flex;
    gap: 10px;
    align-items: center;
  }
}
@media (min-width: 1200px) {
  .footer__logo {
    flex-grow: 1;
  }
}
@media (min-width: 768px) {
  .footer__logo-image {
    width: 50px;
    height: 50px;
  }
}
@media (min-width: 768px) {
  .footer__logo-name {
    display: block;
    max-width: min-content;
    font-size: 8px;
    line-height: 12px;
    text-transform: uppercase;
    margin: 0;
  }
}
@media (min-width: 1200px) {
  .footer__address {
    margin-right: 60px;
  }
}
.logo {
  display: grid;
  grid-template-columns: auto minmax(160px, 300px);
  align-items: center;
}
@media (min-width: 768px) {
  .logo {
    grid-template-columns: auto minmax(300px, 1fr);
  }
}
@media (min-width: 1200px) {
  .logo {
    display: flex;
  }
}
.logo:not(.logo--disable):hover,
.logo:not(.logo--disable):focus {
  opacity: 0.8;
}
.logo:not(.logo--disable):active {
  opacity: 0.6;
}
.logo__name {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: calc(0.3rem + 1.23vw);
  line-height: calc(0.6rem + 1.4vw);
  font-weight: 500;
  color: #000000;
  text-transform: uppercase;
  min-width: 166px;
  margin: 0 10px;
}
@media (min-width: 768px) {
  .logo__name {
    font-size: 22px;
    line-height: 28px;
    margin: 0 0 0 15px;
    text-align: center;
    letter-spacing: 1px;
  }
}
@media (min-width: 1200px) {
  .logo__name {
    font-size: 10px;
    line-height: 14px;
    text-align: left;
    min-width: inherit;
    width: min-content;
  }
}
.logo__name--no-wrap {
  margin: 0;
  white-space: nowrap;
}
.main-nav {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1200px) {
  .main-nav {
    flex-direction: row;
    justify-content: space-between;
    width: 1050px;
    margin: 0 auto;
  }
}
.main-nav__toggle {
  position: relative;
  width: 31px;
  height: 22px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed {
  border-bottom: 2px solid rgba(171, 171, 173, 0.43);
  box-shadow: 0 1px 4px rgba(171, 171, 173, 0.5);
  box-sizing: border-box;
}
@media (min-width: 1200px) {
  .main-nav--closed {
    border-bottom: none;
    box-shadow: none;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 25px;
  height: 3px;
  background-color: #0c317a;
  box-shadow: 0 8px 0 0 #0c317a, 0 16px 0 0 #0c317a;
}
.main-nav--closed .main-nav__toggle:active::before {
  opacity: 50%;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list-wrapper {
    display: none;
  }
}
.main-nav--opened {
  position: relative;
  margin-bottom: 2px;
}
@media (min-width: 768px) {
  .main-nav--opened {
    border-bottom: 2px solid rgba(171, 171, 173, 0.43);
    box-shadow: 0 1px 4px rgba(171, 171, 173, 0.5);
  }
}
@media (min-width: 1200px) {
  .main-nav--opened {
    border: none;
    box-shadow: none;
  }
}
.main-nav--opened .main-nav__list {
  position: absolute;
  z-index: 3;
  border-bottom: 1px solid rgba(171, 171, 173, 0.43);
  box-shadow: 0 1px 5px -3px rgba(171, 171, 173, 0.5);
}
@media (min-width: 768px) {
  .main-nav--opened .main-nav__list {
    position: static;
    border: none;
    box-shadow: none;
  }
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 41%;
  right: 0;
  width: 25px;
  height: 2px;
  background-color: #0c317a;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  opacity: 50%;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__list-wrapper {
  display: block;
}
.main-nav--nojs .main-nav__item:first-child {
  border-top: 1px solid rgba(171, 171, 173, 0.18);
}
.main-nav__block {
  min-width: 280px;
  margin: 12px 6.25%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}
@media (min-width: 768px) {
  .main-nav__block {
    margin: 15px 0;
  }
}
@media (min-width: 1200px) {
  .main-nav__block {
    min-width: max-content;
    margin: 10px 0;
  }
}
@media (min-width: 768px) {
  .main-nav__logo {
    width: 668px;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .main-nav__logo {
    width: auto;
    margin-right: 40px;
  }
}
@media (min-width: 768px) {
  .main-nav__list-wrapper {
    border-top: 1px solid rgba(171, 171, 173, 0.43);
  }
}
@media (min-width: 1200px) {
  .main-nav__list-wrapper {
    display: flex;
    border-top: none;
  }
}
.main-nav__list {
  width: 100%;
  margin: 0;
  text-align: center;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 668px;
    margin: 0 auto;
  }
}
@media (min-width: 1200px) {
  .main-nav__list {
    align-items: center;
    row-gap: 30px;
    height: 100%;
    margin: 0;
  }
}
.main-nav__item {
  position: relative;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.main-nav__item:not(:last-child, :first-child) {
  border-top: 1px solid rgba(171, 171, 173, 0.18);
}
@media (min-width: 768px) {
  .main-nav__item:not(:last-child, :first-child) {
    vertical-align: middle;
    border: none;
  }
}
.main-nav__item:not(.submenu) {
  padding: 15px 0;
}
@media (min-width: 1200px) {
  .main-nav__item:not(.submenu) {
    padding: 0;
  }
}
.main-nav__link:not(.main-nav__link--active) {
  color: #444444;
  cursor: pointer;
}
.main-nav__link:not(.main-nav__link--active):hover,
.main-nav__link:not(.main-nav__link--active) :focus {
  color: #526488;
}
.main-nav__link:not(.main-nav__link--active):active {
  color: #0c317a;
}
.main-nav__link::before {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
}
.main-nav__link--active {
  color: #0c317a;
  font-weight: 500;
}
.main-nav__link--active:active {
  color: #0c317a;
}
@media (min-width: 1200px) {
  .submenu {
    height: 100%;
  }
}
.submenu--closed .submenu__list {
  display: none;
}
.submenu--closed {
  border-bottom: 2px solid rgba(171, 171, 173, 0.18);
}
.submenu--opened .submenu-list {
  display: block;
}
.submenu--nojs .submenu__list {
  display: block;
}
.submenu--nojs.submenu--closed {
  border-bottom: none;
}
.submenu__nav {
  position: relative;
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: center;
  padding: 15px 0;
}
@media (min-width: 1200px) {
  .submenu__nav {
    height: 100%;
    padding: 0;
  }
}
.submenu__title {
  margin: 0 8px 0 0;
}
.submenu__title--active {
  color: #0c317a;
  font-weight: 500;
}
.submenu__toggle {
  border: none;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
}
.submenu__toggle::before {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  width: 100%;
  height: calc(100% + 5px);
}
.submenu__toggle:hover + .submenu__title,
.submenu__toggle:focus + .submenu__title {
  color: #526488;
}
.submenu__toggle svg {
  position: absolute;
  top: 50%;
  stroke: #787171;
  transform-origin: 50% 30%;
  transform: rotate(180deg) translateY(2px);
}
.submenu__toggle:hover path,
.submenu__toggle:focus path {
  stroke: #526488;
}
.submenu__toggle--active path {
  stroke: #0c317a;
}
.submenu__toggle:active path {
  opacity: 50%;
}
.submenu__list {
  padding: 20px 0;
  background-color: rgba(233, 241, 255, 0.5);
  border-top: 1px solid rgba(171, 171, 173, 0.4);
  border-bottom: 1px solid rgba(171, 171, 173, 0.4);
}
@media (min-width: 768px) {
  .submenu__list {
    position: absolute;
    bottom: 0;
    z-index: 3;
    transform: translateY(calc(100% + 3px));
    width: 280px;
    padding: 25px 20px;
    box-sizing: border-box;
    background-color: #ffffff;
    border: 1px solid rgba(171, 171, 173, 0.4);
    box-shadow: 0 1px 4px rgba(171, 171, 173, 0.5);
  }
}
.submenu__item {
  position: relative;
  margin: 0 6.25%;
