  flex-direction: column;
  margin: 30vh 0;
  flex-shrink: 0;
  background: #ffffff;
  min-width: 280px;
  max-width: 80%;
  overflow: visible;
}
@media (min-width: 768px) {
  .doc-modal__window {
    min-width: 400px;
    max-width: 45%;
  }
}
@media (min-width: 1200px) {
  .doc-modal__window {
    min-width: 700px;
  }
}
.doc-modal__toggle {
  position: relative;
  border: none;
  background-color: transparent;
}
.doc-modal__toggle::before,
.doc-modal__toggle::after {
  content: "";
  position: absolute;
  top: -25px;
  right: -5px;
  width: 25px;
  height: 3px;
  border-radius: 2px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .doc-modal__toggle::before,
  .doc-modal__toggle::after {
    top: -23px;
    right: -45px;
    width: 32px;
    height: 4px;
  }
}
@media (min-width: 1200px) {
  .doc-modal__toggle::before,
  .doc-modal__toggle::after {
    right: -65px;
    width: 45px;
    height: 5px;
  }
}
.doc-modal__toggle::before {
  transform: rotate(45deg);
}
.doc-modal__toggle::after {
  transform: rotate(-45deg);
}
